	module.exports = {"data":{"markdownRemark":{"html":"<h2>Area of a Sector</h2>\n<p>A sector is the portion of a circle enclosed by two radii and one arc, where the smaller area is known as the Minor and the larger area is known as the Major sector. </p>\n<p><img src=\"https://upload.wikimedia.org/wikipedia/commons/d/da/Circle_arc.svg\" alt=\"Circle Sector\" title=\"A circle showing Minor and Major sector\"></p>\n<p>Minor sector shaded in green.\n<em>L</em> is the arc length.\n<em>θ</em> is the angle in radians.\n<em>r</em> is the radius.</p>\n<p><strong>The Area of a sector can be obtained by multiplying the circle’s area by the ratio of the angle <em>(θ°)</em> and <em>2π (360°)</em> as follows</strong>:</p>\n<p>A = <em>πr<sup>2</sup> * (θ°/360)</em></p>\n<h3>Example:</h3>\n<p>A circle has a radius of 5 cm. Calculate the area of a sector when the angle made by the radii is 60°. As an approximation for pi, we will use 22/7.</p>\n<p>A = <em>(22/7) * 5<sup>2</sup> * (60°/360°)</em></p>\n<p>A = <strong>13.095 cm<sup>2</sup></strong></p>\n<h4>More Information:</h4>\n<ul>\n<li><a href=\"https://en.wikipedia.org/wiki/Circular_sector\">Circular Sector</a></li>\n</ul>","fields":{"slug":"/mathematics/area-of-a-sector/"},"frontmatter":{"title":"Area of a Sector"}}},"pathContext":{"slug":"/mathematics/area-of-a-sector/"}}

